import React,{useState} from 'react';
import PropTypes from 'prop-types';
import { Panel, List, Button, Group, Div, Avatar, PanelHeader,
  		Epic, Tabbar, TabbarItem, View} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';

const Home = (props) => {
	let [activeStory, setActiveStory] = useState('feed');
	let onStoryChange = (e) => {
		setActiveStory(e.currentTarget.dataset.story);
	}
	return 	<Panel id={props.id}>
		<Epic activeStory={activeStory} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'feed'}
					data-story="feed"
					text="Новости"
				><Icon28ChevronBack/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'discover'}
					data-story="discover"
					text="Поиск"
				><Icon28ChevronBack/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'messages'}
					data-story="messages"
					label="12"
					text="Сообщения"
				><Icon28ChevronBack/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'notifications'}
					data-story="notifications"
					text="Уведомлен."
				><Icon28ChevronBack/></TabbarItem>

			</Tabbar>
		}>
			<View id="feed" activePanel="feed">
				<Panel id="feed">
					<PanelHeader>Новости</PanelHeader>
				</Panel>
			</View>
			<View id="discover" activePanel="discover">
				<Panel id="discover">
					<PanelHeader>Поиск</PanelHeader>
				</Panel>
			</View>
			<View id="messages" activePanel="messages">
				<Panel id="messages">
					<PanelHeader>Сообщения</PanelHeader>
				</Panel>
			</View>
			<View id="notifications" activePanel="notifications">
				<Panel id="notifications">
					<PanelHeader>Уведомления</PanelHeader>
				</Panel>
			</View>
			<View id="more" activePanel="more">
				<Panel id="more">
					<PanelHeader>Ещё</PanelHeader>
				</Panel>
			</View>
		</Epic>
	</Panel>
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
